:root {
    --modal-display: flex;
    --modal-card-scale: 1;
}

.modal-hide {
    --modal-display: none;
}

.modal-scale {
    --modal-card-scale: 1.1;
}

.modal-container {
    position: absolute;
    justify-content: center;
    align-items: center;
    height: calc(100% - 70px);
    width: inherit;
    display: var(--modal-display);
    background: rgba(0, 0, 0, 0.4);
    transition: var(--transition-time);
}

.modal-card {
    border: 1px solid var(--border);
    background-color: var(--navbar);
    width: 500px;
    transition: var(--transition-time);
    scale: var(--modal-card-scale);
}

.modal-header {
    display: flex;
    gap: 10px;
    padding: 20px;
    border-bottom: 1px solid var(--border);
}

.modal-header input {
    border: 2px inset black;
    border-right-color: gray;
    border-bottom-color: gray;
    background-color: var(--background);
    width: 100%;
    color: var(--text);
    outline-style: none;
}

.modal-header input::placeholder {
    color: var(--text);
}

.modal-footer {
    display: flex;
    justify-content: center;
    color: var(--text);
}
