@font-face {
    font-family: "Pokemon";
    src: url(/src/fonts/Pokemon\ Solid.ttf);
}

:root {
    --border-pokemon: #0a285f;
}

.margin-container {
    padding: 20px;
}

.card-pokemon-container {
    width: 300px;
    margin: auto;
    border: 10px solid var(--border-pokemon);
    background-color: #ffcc00;
    border-radius: 15px;
    color: #000;
    box-shadow: -10px 10px 0px black;
    text-align: center;
}

.card-pokemon-title {
    margin: 0;
    height: 100px;
    background: url("/src/image/pokemon.png") no-repeat center;
    background-size: 200px 100%;
    border-bottom: 2px solid var(--border-pokemon);
}

.card-pokemon-content {
    font-family: "Pokemon" !important;
    margin: 0px auto;
}

.pokemon-div {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttons-pokemon {
    margin: 10px auto;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-content: center;
}

.buttons-pokemon button {
    font-family: "Pokemon";
    background: #d5a100;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 5px;
    border: 2px solid var(--border-pokemon);
    border-radius: 8px;
    width: 100px;
    height: 40px;
    cursor: pointer;
    transition: 0.5s;
    box-shadow: -2px 2px 0px black;
}

.buttons-pokemon button:hover {
    background-color: var(--border-pokemon);
    color: #ffcc00;
    transform: scale(0.98);
}
