.hide-table {
    --hide-table: none;
}

.table-wraper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 10px;
    width: 100%;
    user-select: none;
}

.table-container {
    display: var(--hide-table);
    background-color: var(--navbar);
    border: 1px solid var(--border);
    width: 25%;
}

.table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    transition: calc(var(--transition-time) / 3);
}

th {
    padding: 10px;
    border-bottom: 2px solid var(--border);
}

.score {
    padding: 0;
}

td {
    padding: 5px 0px;
    border-top: 1px solid var(--border);
}

.player-name {
    min-width: 100px;
    max-width: 100px;
    overflow: hidden;
}

.last-column {
    min-width: 10px;
    max-width: 10px;
}

.table-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
}

.table-buttons .btn {
    background-color: var(--navbar);
    transition: var(--transition-time);
}

.table-buttons .btn:hover {
    background-color: var(--text);
    color: var(--navbar);
    box-shadow: -4px 4px var(--button-shadow);
    transform: translate(5px, -5px);
    border: none;
}

.empty-container {
    background-color: var(--background);
    transition: var(--transition-time);
}

@media (max-width: 1400px) {
    .table-container {
        width: 30%;
    }
}

@media (max-width: 1200px) {
    .table-container {
        width: 30%;
    }
}

@media (max-width: 992px) {
    .table-container {
        width: 40%;
    }
}

@media (max-width: 768px) {
    .table-container {
        width: 320px;
    }
}

@media (max-width: 576px) {
    .table-container {
        width: 320px;
    }
}
