.clicker-container {
    height: calc(100% - 70px);
}

#circle-container {
    position: absolute;
    transform: translate(-50%, -50%);
    user-select: none;
    width: 20px;
    height: 20px;
}

.dot1 {
    user-select: none;
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    -o-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    animation: one 2s infinite;
    -o-animation: one 2s infinite;
    -webkit-animation: one 2s infinite;
    -ms-animation: one 2s infinite;
    -moz-animation: one 2s infinite;
    animation-delay: 0s;
    animation-timing-function: ease-in-out;
    background-color: black;
}

.dot2 {
    user-select: none;
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    -o-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    animation: two 2s infinite;
    -o-animation: two 2s infinite;
    -moz-animation: two 2s infinite;
    -webkit-animation: two 2s infinite;
    -ms-animation: two 2s infinite;
    animation-delay: 0s;
    animation-timing-function: ease-in-out;
    background-color: #cd5c5c;
}

@keyframes one {
    0% {
        width: 0%;
        height: 0%;
        opacity: 1;
    }
    100% {
        width: 100%;
        height: 100%;
        opacity: 0;
    }
}

@keyframes two {
    0% {
        width: 0%;
        height: 0%;
        opacity: 1;
    }
    50% {
        width: 0%;
        height: 0%;
        opacity: 1;
    }
    100% {
        width: 100%;
        height: 100%;
        opacity: 0;
    }
}
